@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Medium") format("woff2"), url("../fonts/Raleway-Medium") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Work Sans";
  src: url("../fonts/WorkSans-Regular.woff2") format("woff2"), url("../fonts/WorkSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
:root {
  --font-family: "Montserrat", sans-serif;
  --second-family: "Work Sans", sans-serif;
  --third-family: "Raleway", sans-serif;
  --color-main: #ffce22;
  --color-secondary: #4071e6;
  --color-secondary--accent: #204aad;
  --color-text-main: #1f2128;
  --color-text-secondary: #36373c;
  --color-bg-first: #f1f4fa;
  --color-bg-secondary: #fdf8f3;
  --color-border: #e8eaeb;
  --shadow-btn: 0px 6px 10px 0px rgba(141, 114, 19, 0.15);
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

img {
  max-width: 100%;
  object-fit: contain;
  display: block;
  height: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

body {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 18.2px;
  color: var(--color-text-main);
}
@media (max-width: 1920px) {
  body {
    font-size: 14px;
  }
}
@media (max-width: 768px) {
  body {
    font-size: 16px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  max-width: 1920px;
  padding: 0 32px;
  margin: 0 auto;
}

.btn {
  background-color: transparent;
  border: none;
  padding: 11px 18px;
  border-radius: 4px;
  cursor: pointer;
  color: inherit;
}

.link--disabled {
  pointer-events: none;
  opacity: 0.5;
}

.btn--primary {
  padding: 11px 33px;
  background-color: var(--color-main);
}
.btn--primary:hover {
  box-shadow: var(--shadow-btn);
}
.btn--primary:active {
  transform: translateY(2px);
}
@media (max-width: 1024px) {
  .btn--primary {
    padding: 14px 28px;
  }
}

.btn--secondary {
  background-color: var(--color-bg-first);
}
.btn--secondary:hover {
  background-color: var(--color-main);
}

.title {
  font-size: 30px;
  font-weight: 500;
  line-height: normal;
  position: relative;
}

.arrow-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 32px;
  height: 32px;
  background-color: #fff;
  border: none;
  border-radius: 100px;
  border: 1px solid var(--color-bg-first);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.arrow-btn:hover {
  background-color: var(--color-main);
}

.arrow-link {
  position: relative;
  display: inline-block;
}
.arrow-link .arrow-icon {
  position: absolute;
  right: -17px;
  top: 10px;
  transform: translateY(-50%);
}

.arrow-link:link,
.arrow-link:visited {
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
  color: var(--color-secondary);
}

.arrow-link:hover {
  color: var(--color-secondary--accent);
  transform: scale(1.1);
}

.page-header {
  font-size: 16px;
  display: flex;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 13px;
  position: relative;
}

.page-nav {
  flex: 1;
}
.page-nav__list {
  display: flex;
  gap: 32px;
  margin-left: 30px;
}
@media (max-width: 1024px) {
  .page-nav__list {
    display: none;
    flex-direction: column;
    margin: 0;
    position: absolute;
    left: 0;
    width: 100vw;
    top: 68px;
    padding: 32px;
    z-index: 5;
    background-color: #fff;
    border-bottom: 1px solid var(--color-border);
  }
}
.page-nav .page-nav__list.page-nav__list--opened {
  display: flex;
}
.page-nav__link:link, .page-nav__link:visited {
  color: var(--color-text-main);
}
.page-nav__link:hover {
  font-weight: 500;
}
.page-nav__link:active {
  font-weight: 400;
}
.page-header--opened .page-nav {
  display: block;
}

@media (max-width: 1024px) {
  .user-nav {
    margin-right: 44px;
  }
}

.user-nav__login:hover {
  font-weight: 500;
}

.user-nav__login:active {
  font-weight: 400;
}

.page-header__burger-btn {
  position: relative;
  background-color: transparent;
  width: 24px;
  height: 24px;
  border: none;
  display: none;
}
@media (max-width: 1024px) {
  .page-header__burger-btn {
    display: block;
    position: absolute;
    right: 32px;
    top: 50%;
    transform: translateY(-50%);
  }
}
.page-header__burger-btn span {
  display: block;
  width: 100%;
  height: 1px;
  background-color: var(--color-text-secondary);
  position: absolute;
  left: 0;
}
.page-header__burger-btn span:nth-child(1) {
  top: 1px;
}
.page-header__burger-btn span:nth-child(2) {
  top: 50%;
}
.page-header__burger-btn span:nth-child(3) {
  bottom: 1px;
}
.page-header__burger-btn.page-header__burger-btn--opened span:nth-child(1) {
  display: none;
}
.page-header__burger-btn.page-header__burger-btn--opened span:nth-child(2) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}
.page-header__burger-btn.page-header__burger-btn--opened span:nth-child(3) {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.intro {
  background-color: var(--color-bg-secondary);
}
.intro-wrapper {
  max-width: 1480px;
  width: 100%;
  margin: 0 auto;
  padding: 18px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1920px) {
  .intro-wrapper {
    padding: 38px 84px 39px;
  }
}
@media (max-width: 1440px) {
  .intro-wrapper {
    padding: 80px 115px 57px;
  }
}
@media (max-width: 1280px) {
  .intro-wrapper {
    padding: 80px 74px;
  }
}
@media (max-width: 1024px) {
  .intro-wrapper {
    padding: 32px 47px 40px 47px;
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .intro-wrapper {
    padding: 32px 20px 40px 20px;
  }
}
@media (max-width: 1024px) {
  .intro__content {
    order: 5;
    text-align: center;
    width: 100%;
  }
}
.intro__media {
  display: flex;
  justify-content: flex-end;
}
@media (max-width: 1024) {
  .intro__media {
    order: 1;
    justify-content: center;
    margin-bottom: 18px;
  }
}
.intro__title {
  font-size: 36px;
  font-weight: 500;
  line-height: normal;
  margin-bottom: 15px;
}
@media (max-width: 1280px) {
  .intro__title {
    font-size: 30px;
  }
}
.intro__text {
  line-height: 20.8px;
  color: var(--color-text-secondary);
  margin-bottom: 30px;
}
.intro__link {
  display: inline-block;
  color: var(--color-text-main);
}
@media (max-width: 768px) {
  .intro__link {
    display: block;
    width: 100%;
    padding-top: 14px;
    padding-bottom: 14px;
  }
}
@media (max-width: 1440px) {
  .intro__img {
    width: 437px;
    height: 268px;
  }
}

.preview {
  max-width: 1920px;
  margin: 0 auto;
  padding: 40px 32px 100px;
}
@media (max-width: 1440px) {
  .preview {
    padding: 40px 32px 80px;
  }
}
@media (max-width: 1280px) {
  .preview {
    padding: 40px 16px 100px;
  }
}
@media (max-width: 768px) {
  .preview {
    padding: 40px 16px 80px;
  }
}
@media (max-width: 480px) {
  .preview {
    padding: 40px 20px 64px;
  }
}

.categories {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  align-items: center;
  text-align: center;
}
@media (max-width: 768px) {
  .categories {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}
.categories__link:link, .categories__link:visited {
  color: var(--color-text-main);
  line-height: 20.8px;
  border-radius: 4px;
  display: block;
  padding: 10px 18px;
}
.categories__link:hover {
  background-color: var(--color-bg-first);
}

.preview__slider {
  position: relative;
}
.preview__slider-list {
  display: flex;
  gap: 15px;
  overflow-x: auto;
}
.preview__item {
  width: 202px;
  flex-shrink: 0;
}
.preview__bg {
  background-color: var(--color-bg-first);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 147px;
  margin-bottom: 10px;
  border-radius: 4px;
}
.preview__heading {
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
}
.preview__arrow-btn--prev {
  left: -16px;
}
.preview__arrow-btn--next {
  right: -16px;
}

.templates {
  text-align: center;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 32px 100px;
}
@media (max-width: 1440px) {
  .templates {
    padding: 0 32px 80px;
  }
}
@media (max-width: 768px) {
  .templates {
    padding: 0 16px 64px;
  }
}
.templates__title {
  max-width: 550px;
  margin: 0 auto 15px;
}
.templates__title-decor {
  position: relative;
}
.templates__title-decor::before {
  position: absolute;
  top: -2px;
  left: -1px;
  content: "";
  display: block;
  width: 193px;
  height: 48px;
  background-image: url("../img/decor.svg");
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 5;
}
.templates__subtitle {
  line-height: 20.8px;
  color: var(--color-text-secondary);
  margin-bottom: 30px;
}
.templates__slider-wrapper {
  position: relative;
}
.templates__slider {
  margin-bottom: 30px;
  overflow-x: auto;
}
.templates__list {
  display: flex;
  gap: 15px;
}
.templates__list + .templates__list {
  margin-top: 15px;
}
.templates__item {
  flex-shrink: 0;
}
.templates__arrow-btn--prev {
  left: -16px;
}
.templates__arrow-btn--next {
  right: -16px;
}

.features {
  max-width: 1480px;
  padding: 0 50px 100px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, minmax(441px, 1fr));
  column-gap: 20px;
  row-gap: 80px;
}
@media (max-width: 1440px) {
  .features {
    padding: 0 48px 100px;
    column-gap: 80px;
    grid-template-rows: repeat(3, minmax(318px, 1fr));
  }
}
@media (max-width: 1280px) {
  .features {
    max-width: 1060px;
    padding: 0 20px 100px;
    column-gap: 30px;
    grid-template-rows: repeat(3, minmax(295px, 1fr));
  }
}
@media (max-width: 1024px) {
  .features {
    max-width: 876px;
    column-gap: 20px;
    grid-template-rows: repeat(3, minmax(257px, 1fr));
  }
}
@media (max-width: 768px) {
  .features {
    max-width: 530px;
    padding: 0 15px 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 0;
  }
}
@media (max-width: 480px) {
