}
@media (max-width: 768px) {
  .cta__link {
    display: block;
    max-width: 343px;
    margin: 0 auto;
  }
}

.page-footer {
  position: relative;
}
.page-footer__wrapper {
  max-width: 1920px;
  padding: 0 32px;
}
@media (max-width: 768px) {
  .page-footer__wrapper {
    padding: 0 20px;
  }
}
.page-footer::before {
  content: "";
  position: absolute;
  height: 1px;
  background-color: var(--color-border);
  top: 0;
  left: 0;
  right: 0;
}

.footer-top {
  padding: 40px 0 42px;
  display: grid;
  grid-template-areas: "socials menu" "logo menu";
}
@media (max-width: 1024px) {
  .footer-top {
    grid-template-areas: "logo socials" "menu menu";
    row-gap: 40px;
    padding-bottom: 10px;
  }
}
.footer-top__socials-list {
  grid-area: socials;
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}
@media (max-width: 1024px) {
  .footer-top__socials-list {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 0;
    gap: 24px;
  }
}
.footer-top__socials-item {
  display: flex;
}
.footer-top__socials-link {
  display: inline-block;
  padding-left: 24px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 18px 18px;
}
.footer-top__socials-link:link, .footer-top__socials-link:visited {
  color: var(--color-text-secondary);
}
.footer-top__socials-link:hover {
  transform: scale(1.3);
}
.footer-top__socials-link:hover span {
  color: var(--color-text-main);
  font-weight: 500;
}
@media (max-width: 1024px) {
  .footer-top__socials-link {
    width: 22px;
    height: 22px;
    padding: 0;
    background-position: center;
  }
}
@media (max-width: 1024px) {
  .footer-top__socials-link span {
    display: none;
  }
}
.footer-top__menu-wrapper {
  grid-area: menu;
  display: flex;
  max-width: 1230px;
  justify-content: space-between;
}
@media (max-width: 1024px) {
  .footer-top__menu-wrapper {
    gap: 20px;
    flex-wrap: wrap;
  }
}
.footer-top__menu-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (max-width: 1024px) {
  .footer-top__menu-list {
    margin-bottom: 32px;
  }
}
.footer-top__menu-title {
  margin-bottom: 5px;
  line-height: normal;
}
.footer-top__menu-link:link, .footer-top__menu-link:visited {
  display: inline-block;
  color: var(--color-text-secondary);
}
.footer-top__menu-link:hover {
  color: var(--color-text-main);
  transform: scale(1.1);
  font-weight: 500;
}

.link-instagram {
  background-image: url("../img/instagram.svg");
}

.link-facebook {
  background-image: url("../img/facebook.svg");
}

.link-twitter {
  background-image: url("../img/twitter.svg");
}

.link-pinterest {
  background-image: url("../img/pinterest.svg");
}

.logo-link {
  grid-area: logo;
}

.footer-bottom {
  padding: 16px 0;
  position: relative;
  display: flex;
}
.footer-bottom::before {
  content: "";
  position: absolute;
  height: 1px;
  background-color: var(--color-border);
  top: 0;
  left: -32px;
  width: calc(100% + 64px);
}
.footer-bottom__lang-list {
  margin-right: 24px;
  background-image: url("../img/language.svg"), url("../img/arrow-down.svg");
  background-repeat: no-repeat, no-repeat;
  background-position: left center, right center;
  background-size: 18px 19px, 13px 8px;
}
.footer-bottom__lang-link:link, .footer-bottom__lang-link:visited {
  display: block;
  padding-left: 28px;
  padding-right: 18px;
  color: var(--color-text-secondary);
}
.footer-bottom__lang-link:hover {
  color: var(--color-text-main);
  font-weight: 500;
}
.footer-bottom__copy-link:link, .footer-bottom__copy-link:visited {
  color: var(--color-text-secondary);
}
.footer-bottom__copy-link:hover {
  color: var(--color-text-main);
  font-weight: 500;
}
.footer-bottom__copy-link + .footer-bottom__copy-link {
  margin-left: 15px;
}
.footer-bottom__copyright {
  margin-left: auto;
  color: var(--color-text-secondary);
}